.App {
  text-align: center;
  font-size: 16px;
}

button {
  background-color: blue;
  color: white;
  padding: 5px;
}

#titleDiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 20px;
}


#title {
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 40px;
}

.name {
  color: #ffa800;
  font-size: 25px;
  margin: 0px;
  margin: -5px;
  font-weight: bold;
}

strong {
  color: #ffa800;
}

#loading {
    margin: 20px;
    width: 100px; 
    height: 100px;    
    -webkit-animation: spin 2s infinite linear;    
            animation: spin 2s infinite linear;
}

@-webkit-keyframes spin {
    0%  {-webkit-transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);}   
}


